.cards {
  position: relative;

  @include order-reset;

  @media (min-width: 768px) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 140px 60px;
  }

  @media (min-width: 1440px) {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 144px 80px;
  }
}

.cards__item {
  position: relative;
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  padding: 20px;
  border-bottom: 1px solid var(--light-gray-4);

  @media (min-width: 768px) {
    flex-direction: column;
    padding: 0;
    padding-top: 161px;
    padding-bottom: 40px;
    background-color: var(--light-gray-2);
    border-bottom: none;
  }
}

.cards__item:last-child {
  background-color: var(--basic-white);

  &::before {
    position: absolute;
    content: "";
    top: 35px;
    left: 0;
    right: 0;
    margin: auto;
    width: 30px;
    height: 2px;
    background-color: var(--light-gray-3);
    transform: rotate(90deg);
  }

  &::after {
    position: absolute;
    content: "";
    top: 35px;
    left: 0;
    right: 0;
    margin: auto;
    width: 30px;
    height: 2px;
    background-color: var(--light-gray-3);
  }

  @media (min-width: 768px) {
    border: 2px solid var(--light-gray-2);

    &::before {
      top: 82px;
      width: 60px;
    }

    &::after {
      top: 82px;
      width: 60px;
    }
  }
}

.cards__info-wrapper {
  order: 2;
  width: 50%;
  margin-left: auto;
  padding-bottom: 22px;

  @media (min-width: 768px) {
    width: 100%;
    padding-bottom: 33px;
  }
}

.cards__img-wrapper {
  display: block;
  position: relative;
  order: 1;
  width: 140px;
  text-align: center;

  @media (min-width: 768px) {
    width: 100%;
    padding: 0;
  }
}

.cards__img {
  display: block;
  position: absolute;
  width: 68px;
  height: 86px;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);

  @media (min-width: 768px) {
    width: 161px;
    height: 172px;
  }
}

.cards__img--big {
  width: 82px;
  height: 96px;

  @media (min-width: 768px) {
    width: 168px;
    height: 199px;
  }
}

.cards__order-button {
  order: 3;

  @media (min-width: 768px) {
    width: 75%;
    margin: 0 auto;
    margin-top: auto;
  }
}

.cards__title {
  margin: 0;
  margin-bottom: 15px;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  color: var(--dark-gray-2);

  &--last {
    margin: 0 auto;
    margin-bottom: 5px;
    padding-top: 53px;
    text-transform: uppercase;
  }

  @media (min-width: 768px) {
    width: 40%;
    margin: 0 auto;
    margin-bottom: 21px;
    padding-top: 0;
    font-size: 20px;
    line-height: 24px;
    text-align: center;

    &--last {
      width: 45%;
    }
  }

  @media (min-width: 1440px) {
    width: 55%;
  }
}

.cards__title br {
  @media (min-width: 768px) {
    display: none;
  }
}

.cards__description {
  width: 100%;
  padding-bottom: 22px;
  border-collapse: collapse;

  @media (min-width: 768px) {
    width: 75%;
    margin: 0 auto;
  }
}

.cards__row {
  margin: 0;
}

.cards__row:last-child {
  padding-bottom: 0;
}

.cards__column {
  padding-bottom: 4px;
  font-family: "Arial", sans-serif;
  font-size: 12px;
  line-height: 16px;

  @media (min-width: 768px) {
    padding-top: 7px;
    padding-bottom: 9px;
    font-size: 16px;
    border-bottom: 1px solid var(--light-gray-radio);
  }

  @media (min-width: 1440px) {
    width: 80%;
  }
}

.cards__column:nth-child(2n) {
  text-align: right;
}

.cards__item:last-child .cards__column {
  margin: 0 auto;
  margin-bottom: 21px;
  text-align: center;
  border-bottom: none;

  @media (min-width: 768px) {
    width: 60%;
    padding: 0;
    text-align: center;
    border-bottom: none;
  }
}
