.contacts--inner {
  background-color: var(--light-gray-2);

  @media (min-width: 1440px) {
    background-color: var(--basic-white);
  }
}

.contacts__text {
  width: 49%;
  color: var(--dark-gray);
  text-transform: uppercase;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;

  @media (min-width: 768px) {
    font-size: 20px;
    line-height: 25px;
  }
}

.contacts__address {
  margin: 0;
  font-family: "Arial", sans-serif;
  line-height: 20px;

  @media (min-width: 768px) {
    font-size: 16px;
    line-height: 18px;
    text-align: right;
  }
}

.contacts__address-text {
  margin-bottom: 3px;

  @media (min-width: 768px) {
    display: block;
    margin-bottom: 20px;
  }
}
