.features {
  padding-bottom: 42px;

  @media (min-width: 768px) {
    padding-bottom: 100px;
    border-bottom: 1px solid var(--basic-decorative-color);
  }

  @media (min-width: 1440px) {
    padding-bottom: 129px;
    border: none;
  }
}

.features__title {
  margin-bottom: 37px;

  @media (min-width: 768px) {
    margin-bottom: 72px;
  }
}
