.social {
  @include order-reset;
  width: 130px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 22px;
  padding: 20px 0;
  align-items: center;

  @media (min-width: 768px) {
    padding: 0;
    border: none;
  }
}

.social__link-vk {
  @media (min-width: 768px) {
    width: 43px;
    height: 21px;
  }
}

.social__link-insta {
  @media (min-width: 768px) {
    width: 23px;
    height: 23px;
  }
}

.social__link-fb {
  @media (min-width: 768px) {
    width: 28px;
    height: 33px;
  }
}

.social__link:hover svg {
  fill: var(--basic-green);
}

.social__link-fb:hover svg {
  stroke: var(--basic-green);
}

.social__link:active svg {
  fill: var(--basic-green-30);
}

.social__link-fb:active svg {
  stroke: var(--basic-green-30);
}

.social__link-fb:active path {
  fill: none;
  stroke: var(--basic-green-30);
}
